.App {
  margin-left: auto;
  margin-right: auto;
  padding-left: 60px;
  padding-right: 60px;
  text-align: left;
  color: white;
  max-width: 1800px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #070715fb;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: calc(8px + 1vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navLink {
  color: white;
  font-weight: bold;
  margin-left: 20px;
  text-decoration: none;
  padding: 10px;
}

.navLink:hover {
  background-color: white;
  color: #070715fb;
}
.skills-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  margin: 0 auto;
  margin-top: 50px;
}

.skill-section {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  border-top:#fffffffb 5px solid;
}

.skill-section img {
  flex: 0 0 20%; 
  max-height: 200px; 
  object-fit: contain;
  margin: 15px;
}

.skill-section .text-content {
  flex: 1; 
  padding: 0 15px; 
}

@media (max-width: 768px) {
  .skill-section {
    flex-direction: column;
    text-align: center;
  }

  .skill-section img {
    max-width: 80%; 
    margin-bottom: 15px;
  }

  .skill-section .text-content {
    flex: none; 
  }
}

.portfolio-container {
  padding: 20px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 20px;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto;
  border: 1px solid #3d3d3d;
  border-radius: 8px;
  padding: 10px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.card-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 4px;
}

.card-content {
  margin-top: 10px;
}

.card-wrapper {
  transition: transform 0.3s ease;  
  transform-origin: center;
}


.card.active {
  z-index: 10;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 45%;
  height: 75%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #3d3d3d;
}

.card.active .card-wrapper {
  transform: scale(1.5);
}

.card.active .card-image {
  height: 500px;
  object-fit: contain;
  border-radius: 4px;

  margin-top: 12%;
}

